<template>
  <div id="all_3">
    <div class="title">
      <img src="../assets/titlenew.jpg" style="width: 100%;height: 35%"/>
    </div>

    <!-- /container -->
    <hr />

    <div id="all_2">
      <div class="card">
        <form id="form1" class="form-signin" @submit="register"><!--@submit="register"-->
          <div class="card-header">
            <p class="myMOUSE" style="float: left;" @click="back_to_login"></p>
          </div>
          <div class="card-body">
            <!--  -->
            <div class="form-group row">
              <label class="col-sm-2 col-form-label"></label>
              <div class="col-sm-10">
                <input
                  type="email"
                  v-model="username"
                  pattern="[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,}$"
                  class="form-control"
                  placeholder=""
                  required
                  autofocus
                />
              </div>
            </div>
            <br />
       
            <div class="form-group row">
              <label class="col-sm-2 col-form-label"></label>
              <div class="col-sm-10">
                <input
                  type="email"
                  v-model="email"
                  pattern="[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,}$"
                  class="form-control"
                  placeholder=""
                  required
                  autofocus
                />
              </div>
            </div>
            <br />
            <div class="form-group row">
              <label class="col-sm-2 col-form-label"></label>
              <div class="col-sm-10">
                <input
                  type=""
                  v-model="password"
                  pattern="^(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}$"
                  class="form-control"
                  placeholder=""
                  title="8"
                  required
                />
              </div>
            </div>
            <br />

            <div class="form-group row">
              <label class="col-sm-2 col-form-label"></label>
              <div class="col-sm-10">
                <input
                  type=""
                  v-model="password2"
                  pattern="^(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}$"
                  class="form-control"
                  placeholder=""
                  title="8"
                  required
                />
              </div>
            </div>
            <br />

            <div class="form-group row">
              <label class="col-sm-2 col-form-label"></label>
              <div class="col-sm-10">
                <input
                  type="text"
                  v-model="m_name"
                  class="form-control"
                  placeholder=""
                  required
                />
              </div>
            </div>
            <br />

            <div class="form-group row">
              <label class="col-sm-2 col-form-label"></label>
              <div class="col-sm-10">
                <input
                  type="text"
                  v-model="c_name"
                  class="form-control"
                  placeholder=""
                  required
                />
              </div>
            </div>
            <br />

            <div class="form-group row">
              <label class="col-sm-2 col-form-label"></label>
              <div class="col-sm-10">
                <input
                  type="text"
                  v-model="c_num"
                  class="form-control"
                  pattern="\[0-9]{8}"
                  placeholder=""
                  title=""
                  required
                />
              </div>
            </div>
            <br />

            <div class="form-group row">
              <label class="col-sm-2 col-form-label"></label>
              <div class="col-sm-10">
                <input type="tel" v-model="phone" class="form-control" placeholder="" required />
              </div>
            </div>
            <br />

            <div class="form-group row">
              <label class="col-sm-2 col-form-label"></label>
              <div class="col-sm-10">
                <select class="browser-default custom-select" v-model="workers" required>
                  <!-- <option value disabled selected></option> -->
                  <option v-for="(item, index) in forms2" :value="item">({{index + 1}}){{item}}</option>
                </select>
              </div>
            </div>
            <br />

            <div class="form-group row">
              <label class="col-sm-2 col-form-label"></label>
              <div class="col-sm-10">
                <select class="browser-default custom-select" v-model="industry" required>
                  <option v-for="(item, index) in forms" :value="item">({{index + 1}}){{item}}</option>
                </select>
              </div>
            </div>
            <br />

            <div class="form-group row">
              <label class="col-sm-2 col-form-label"></label>
              <div class="col-sm-10">
                <div class="bbbox">
                  <p align="left">
                    <b>()</b>
                    <br />
                    <br />
                    ..()
                    
                    <br />
                    <br />
                    
                    <br />
                    <br />
                    https://www.iii.org.tw/
                    () 
                    <br />() 
                    <br />() 
                    <br />() 
                    <br />() 
                    <br />
                    <br />
                    
                    <br />
                    <br />
                  </p>
                  <h2>
                    <b>
                      <p align="left"></p>
                    </b>
                  </h2>
                  <p align="left">
                    
                    <br />
                  </p>
                  <p align="left">
                    
                    <br />
                  </p>
                </div>
              </div>
            </div>
            <br />

            <div align="center">
              <input type="checkbox" required="required" />
              <br />
              <br />
              <button class="btn btn-primary" @click.prevent='register()' ></button>
            </div>
          </div>
        </form>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";

export default {
  data() {
    return {
      forms: [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        ""
      ],
      forms2: [
        "5(5)",
        "5~10()",
        "11~50",
        "51~100",
        "101"
      ],
      email: "",
      username: "",
      password: "",
      password2: "",
      m_name: "",
      c_name: "",
      c_num: "",
      phone: "",
      industry: "",
      workers: "101"
    };
  },
  methods: {
    back_to_login() {
      this.$router.push("/view/view_home");///view/view_login
    },
    register() {
      var self = this;
      //
      //let phone_pattern_error = false;
      /*if (!String(this.phone).match(/^[0-9]{2}-[0-9]{7,8}$/)) {
        //02-66072131
        if (!String(this.phone).match(/^[0-9]{2}-[0-9]{7,8}#[0-9]{2,6}$/)) {
          //02-66072131#1234
          if (!String(this.phone).match(/^09[0-9]{8}$/)) {
            //0987654321
            phone_pattern_error = true;
            self.$swal({
              icon: "error",
              title: "",
              timer: 1500
            });
          }
        }
      } else {
      }*/
      //if (phone_pattern_error === false) {
        if (this.password === this.password2) {
          this.$swal({
            text: `?`,
            icon: "question",
            showCancelButton: true,
            confirmButtonText: "",
            cancelButtonText: "",
            preConfirm: () => {
              return this.axios
                .post(
                  "/rise_api/public/index.php/view/register",
                  {
                    username:this.username,
                    email: this.email,
                    industry: this.industry,
                    password: this.password,
                    m_name: this.m_name,
                    c_name: this.c_name,
                    c_num: this.c_num,
                    phone: this.phone,
                    workers: this.workers
                  }
                )
                .then(response => {

                 // console.log(response.data);

                  if (response.data[0].status == "1") {

                    
                    this.$swal({
                      title: "",
                      allowEscapeKey: false,
                      allowOutsideClick: false,
                      timer: 2500,
                      onOpen: () => {
                        this.$swal.showLoading();
                        this.axios
                          .post(
                            "/rise_api/public/index.php/email/send",
                            {
                              email: this.email,
                              token: response.data[0].token
                            }
                          )
                          .then(response => {
                            if (response.status == 200) {
                              this.$swal({
                                icon: "success",
                                title: "",
                                showConfirmButton: false,
                                timer: 2500
                              }).then(() => {
                                this.$router.push({
                                  name: "view_login",
                                  params: { userId: this.email } //login 
                                });
                              });
                            }
                          });
                      }
                    });
                  }else if (response.data.indexOf("SQLSTATE[23000]") != -1) {
                    //
                    this.$swal({
                      icon: "error",
                      text: "!"
                    });
                  }
                  //(debug) 
                  else {
                    this.$swal({
                      icon: "error",
                      text: "!"
                    });
                  }
                });
            }
          });
        } else {
          this.$swal({
            icon: "error",
            text: "!"
          });
        }
      //}
    }
  }
};
</script>

<style>
#all_2 {
  width: 90%;
  margin: auto;
}
#all_3 {
  width: 70%;
  margin: auto;
}
.form-signin input[type="email"] {
  /*  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;*/
  text-indent: 35px;
  background-size: 30px 35px;
  background-image: url("../assets/email.png");
  background-position: 7px 4px;
  background-repeat: no-repeat;
}
.form-signin input[type="password"] {
  /*  border-top-left-radius: 0;
  border-top-right-radius: 0;*/
  text-indent: 35px;
  background-size: 25px 25px;
  background-image: url("../assets/password.png");
  background-position: 9px 9px;
  background-repeat: no-repeat;
}
.bbbox {
  border: 1px solid #cccccc;
  width: auto;
  height: 200px;
  background-color: #ffffff;
  padding: 5px;
  overflow: auto;
}
.myMOUSE:hover {
  cursor: pointer;
  color: #0056c2;
  text-decoration: underline;
}
</style>



