import Vue from "vue";
import Router from "vue-router";
import axios from "axios";

//
import FrontLayout from "@/view/Layout"; //banner
import FrontForm from "@/view/Form"; //
import view_home from "@/view/home"; //
import view_about from "@/view/about"; //
import view_register from "@/view/register"; //
import answer from "@/view/answer"; //
import answerType1 from "@/view/answer1/index.vue"; //
import answerView1 from "@/view/answer1/view.vue"; //  - 1
import answerType2 from "@/view/answer2/index.vue"; //

import appreciationContent from "@/view/appreciationContent"; //
import answer_history from "@/view/answer_history"; //()
import answer_show from "@/view/answer_show"; // 
import view_login from "@/view/login"; //
import register_check from "@/view/register_check"; //
import password_forget from "@/view/password_forget"; //
import password_reset from "@/view/password_reset"; //
import error404 from "@/view/error404"; //404
import user_update from "@/view/user_update"; //

Vue.use(Router);

let router = new Router({
  routes: [
    {
      path: "/",
      redirect: "/view/view_home"
    },
    //
    {
      path: "/view",
      component: FrontLayout,
      name: "View",
      children: [
        {
          path: "form/:id",
          name: "FrontForm",
          component: FrontForm
        },
        {
          path: "form/:id/:preview",
          name: "preview",
          component: FrontForm
        },
        {
          path: "view_home",
          name: "view_home",
          component: view_home
        },
        {
          path: "view_home/:ssoFlag/:id/:password/:formId/:time",
          name: "view_home",
          component: view_home
        },
        {
          path: "view_home/:ssoFlag/:id/:password/:formId/:time/:customer",
          name: "view_home",
          component: view_home
        },
        {
          path: "view_about",
          name: "view_about",
          component: view_about
        },
        {
          path: "view_register",
          name: "view_register",
          component: view_register
        },
        {
          path: "view_login",
          name: "view_login",
          component: view_login
        },
        {
          path: "answer",
          name: "answer",
          component: answer
        },
        {
          path: "answer_1",
          name: "answer_1",
          component: answerType1
        },
        {
          path: "answer_2",
          name: "answer_2",
          component: answerType2
        },
        {
          path: "answer_report_1/:answerEid",
          name: "answer_report_1",
          component: answerView1
        },
        {
          path: "answer_view_1/:answerId",
          name: "answer_view_1",
          component: answerView1
        },
        {
          path: "answer_view_2/:answerId",
          name: "answer_view_2",
          component: answerType2
        },
        {
          path: "answer_report_2/:answerEid",
          name: "answer_report_2",
          component: answerType2
        },
        {
          path: "appreciationContent",
          name: "appreciationContent",
          component: appreciationContent
        },
        {
          path: "answer_show",
          name: "answer_show",
          component: answer_show
        },
        {
          path: "answer_show/:formId/:time",
          name: "answer_show",
          component: answer_show
        },
        {
          path: "answer_history",
          name: "answer_history",
          component: answer_history
        },
        {
          path: "answer_history/:formId/:companyName/:time",
          name: "answer_history",
          component: answer_history
        },
        {
          path: "view_registered_check/:token", //
          name: "view_registered_check",
          component: register_check
        },
        {
          path: "view_password_forget", //
          name: "view_password_forget",
          component: password_forget
        },
        {
          path: "view_password_reset/:token", //
          name: "view_password_reset",
          component: password_reset
        },
        {
          path: "user_update",
          name: "user_update",
          component: user_update
        }
      ]
    },
    {
      path: "*",
      component: error404
    }
  ]
});

router.beforeEach((to, from, next) => {
  //
  // console.log(localStorage);
  /*if(localStorage.length > 1 && localStorage.username!="guest"){
    if(parseInt(new Date().getTime()/1000)-localStorage.view_time>14400){
      localStorage.clear();
      Vue.swal.fire({
        icon:'error',
        title:'Oops.......',
        text:'',
      }).then(() => {
        next('/view/view_login')
      })
    }
  }*/

  if (to.name == "FrontForm") {
    axios
      .post("/rise_api/public/index.php/view/login_check_token", {
        username: localStorage.view_username,
        token: localStorage.view_token
      })
      .then(response => {
        if (response.data != "1") {
          Vue.swal
            .fire({
              icon: "error",
              title: "",
              text: "",
              showCancelButton: true,
              confirmButtonText: " !",
              cancelButtonText: "()"
            })
            .then(result => {
              if (result.value) {
                next("/view/view_login");
              } else {
                next("/view/view_register");
              }
            });
        } else {
          localStorage.setItem("view_status", "1");
        }
      });
    next();
  } else {
    next();
  }
});

export default router;



