<template>
  <div id="all_3">
    <div class="title">
      <img src="../assets/titlenew.jpg" style="width: 100%;height: 35%"/>
    </div>
    <!-- /container -->
    <hr />

    <div id="all_2">
      <div class="card">
        <div class="card-header">
          <p class="myMOUSE" style="float: left;" @click="back_to_login"><</p>
        </div>
        <div class="card-body">
          <!--  -->
          <p></p>
          <div class="form-group row">
            <label class="col-sm-2 col-form-label"></label>
            <div class="col-sm-10">
              <input v-model="email" class="form-control" placeholder="E-mail" />
            </div>
          </div>
          <!--  -->
          <div align="center">
            <button class="btn btn-primary login_button" @click="login()"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";

export default {
  data() {
    return {
      email: ""
    };
  },
  created: function() {
    var _this = this;
    document.onkeydown = function(e) {
      let key = window.event.keyCode;
      if (key == 13) {
        _this.login();
      }
    };
  },
  methods: {
    back_to_login() {
      this.$router.push("/view/view_login");
    },
    login() {
      let username = this.email;
      if (username != "") {
        this.$swal({
          text: `?`,
          icon: "question",
          showCancelButton: true,
          confirmButtonText: "",
          cancelButtonText: "",
          preConfirm: () => {
            this.$swal({
              title: "",
              allowEscapeKey: false,
              allowOutsideClick: false,
              timer: 2500,
              onOpen: () => {
                this.$swal.showLoading();
                return this.axios
                  .post(
                    "/rise_api/public/index.php/email/password_forget",
                    {
                      email: this.email
                    }
                  )
                  .then(response => {
                    console.log(response);
                    if (response.status == 200) {
                      this.$swal({
                        icon: "success",
                        title: "",
                        showConfirmButton: false,
                        timer: 2500
                      });
                    }
                    //
                    else if (response.status != 200) {
                      this.$swal({
                        icon: "error",
                        text: ""
                      });
                    }
                    //(debug) 
                    else {
                      this.$swal({
                        icon: "error",
                        text: "!"
                      });
                    }
                  });
              }
            });
          }
        });
      } else {
        this.$swal({
          icon: "error",
          text: ""
        });
      }
    }
  }
};
</script>

<style>
#all_2 {
  width: 90%;
  margin: auto;
}
#all_3 {
  width: 70%;
  margin: auto;
}

.login_button {
  display: block;
  margin: 10px auto;
  width: 180px;
  color: #ffffff;
}

.myMOUSE:hover {
  cursor: pointer;
  color: #0056c2;
  text-decoration: underline;
}
</style>



