<template>
  <div id="all_3">
    <!-- /container -->
    <hr />
    <div id="all_2">
      <div class="card">
        <div class="card-header"></div>
        <div class="card-body">
          <div class="form-group row">
            <label class="col-sm-2 col-form-label"></label>
            <div class="col-sm-10">
              <input v-model="password" class="form-control" type="password" />
            </div>
          </div>
          <div class="form-group row">
            <label class="col-sm-2 col-form-label"></label>
            <div class="col-sm-10">
              <input v-model="password2" class="form-control" type="password"/>
            </div>
          </div>
          <!--  -->
          <div align="center">
            <button class="btn btn-primary login_button" @click="password_reset()"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";

export default {
  data() {
    return {
      password: "",
      password2: ""
    };
  },
  created() {
    // this.password_reset();
    this.read_token();
  },
  methods: {
    //(token)
    read_token() {
      let reset_token = location.href.split("password_reset/");
      this.axios
        .post(
          "/rise_api/public/index.php/email/password_check_token",
          {
            token: reset_token[1]
          }
        )
        .then(response => {
          if (response.data == "0") {
            this.$swal({
              icon: "error",
              title: "",
              showConfirmButton: false,
              timer: 2500
            }).then(() => {
              this.$router.push({
                name: "view_home"
              });
            });
          }
          //
          else if (response.data > 10800) {
            this.$swal({
              icon: "error",
              title: "",
              showConfirmButton: false,
              timer: 2500
            }).then(() => {
              this.$router.push({
                name: "view_home"
              });
            });
          }
          //token 
          else {
          }
        });
    },
    //
    password_reset() {
      let reset_token = location.href.split("password_reset/");
      if (this.password == this.password2) {
        if (
          !String(this.password).match(/^(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}$/)
        ) {
          this.$swal({
            icon: "error",
            text: "8"
          });
        } else {
          this.axios
            .put(
              "/rise_api/public/index.php/email/password_reset",
              {
                token: reset_token[1],
                password: this.password
              }
            )
            .then(response => {
              if (response.data == "1") {
                this.$swal({
                  icon: "success",
                  title: "",
                  showConfirmButton: false,
                  timer: 1500
                }).then(() => {
                  this.$router.push({
                    name: "view_login"
                    // params: { email: this.email }  //   (this.email)
                  });
                });
              } else if (response.data == "0") {
                this.$swal({
                  icon: "error",
                  title: "",
                  showConfirmButton: false,
                  timer: 1500
                }).then(() => {
                  this.$router.push({
                    name: "view_login"
                    // params: { email: this.email }  //   (this.email)
                  });
                });
              }
              //
              else {
                this.$swal({
                  icon: "error",
                  title: ":mitech@iii.org.tw",
                  showConfirmButton: false,
                  timer: 3000
                }).then(() => {
                  this.$router.push({
                    name: "view_home"
                  });
                });
              }
            });
        }
      } else {
        this.$swal({
          icon: "error",
          text: "!"
        });
      }
    }
  }
};
</script>

<style>
#all_2 {
  width: 90%;
  margin: auto;
}
#all_3 {
  width: 70%;
  margin: auto;
}
</style>



