<template>
  <div id="all_3">
    <!-- /container -->
    <hr />

    <div id="all_2">
      <div class="card">
        <!--form id="form1" class="form-signin" @submit="register"--><!---->
        <div class="card-header">
          
        </div>
        <div class="card-body">
          <!--  -->
          <div class="form-group row">
            <label class="col-sm-2 col-form-label"></label>
            <div class="col-sm-10">
            {{ username }}
            </div>
          </div>
          <br />
          <!--  -->
          <div class="form-group row">
            <label class="col-sm-2 col-form-label"></label>
            <div class="col-sm-10">
              <input
                type="password"
                v-model="password"
                pattern="^(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}$"
                class="form-control"
                placeholder=""
                title="8"
                required
              />
            </div>
          </div>
          <br />

          <div class="form-group row">
            <label class="col-sm-2 col-form-label"></label>
            <div class="col-sm-10">
              <input
                type="password"
                v-model="password2"
                pattern="^(?=.*\d)(?=.*[a-z])(?=.*[A-Z]).{8,}$"
                class="form-control"
                placeholder=""
                title="8"
                required
              />
            </div>
          </div>
          <br />

          <div class="form-group row">
            <label class="col-sm-2 col-form-label"></label>
            <div class="col-sm-10">
              <input
                type="text"
                v-model="m_name"
                class="form-control"
                placeholder=""
                required
              />
            </div>
          </div>
          <br />

          <div class="form-group row">
            <label class="col-sm-2 col-form-label"></label>
            <div class="col-sm-10" id="app">
              <input
                type="text"
                v-model.lazy="c_name"
                class="form-control"
                placeholder=""
                required
              />
            </div>
          </div>
          <br />

          <div class="form-group row">
            <label class="col-sm-2 col-form-label"></label>
            <div class="col-sm-10">
              <input
                type="text"
                v-model="c_num"
                class="form-control"
                pattern="\[0-9]{8}"
                placeholder=""
                title=""
                required
              />
            </div>
          </div>
          <br />


           <div class="form-group row">
            <label class="col-sm-2 col-form-label"></label>
            <div class="col-sm-10">
              <input
                type="email"
                v-model="email"
                pattern="[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,}$"
                class="form-control"
                placeholder=""
                required
                autofocus
              />
            </div> 
          </div>
        <br />

          <div class="form-group row">
            <label class="col-sm-2 col-form-label"></label>
            <div class="col-sm-10">
              <input
                type="tel"
                v-model="phone"
                class="form-control"
                placeholder=""
                required
              />
            </div>
          </div>
          <br />

          <div class="form-group row">
            <label class="col-sm-2 col-form-label"></label>
            <div class="col-sm-10">
              <select
                class="browser-default custom-select"
                v-model="workers"
                required
              >
                <!-- <option value disabled selected></option> -->
                <option v-for="(item, index) in forms2" :value="item">
                  ({{ index + 1 }}){{ item }}
                </option>
              </select>
            </div>
          </div>
          <br />

          <div class="form-group row">
            <label class="col-sm-2 col-form-label"></label>
            <div class="col-sm-10">
              <select
                class="browser-default custom-select"
                v-model="industry"
                required
              >
                <option v-for="(item, index) in forms" :value="item">
                  ({{ index + 1 }}){{ item }}
                </option>
              </select>
            </div>
          </div>
          <br />
          <div align="center">
            <!--input type="checkbox" required="required" />
              <br />
              <br /-->
            <button class="btn btn-primary" @click="update"></button>
            <router-link class="btn btn-secondary" :to="`/view/view_home`" role="button"></router-link>
          </div>
        </div>
        <!--/form-->
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery";

export default {
  data() {
    return {
      forms: [
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
        "",
      ],
      forms2: [
        "5(5)",
        "5~10()",
        "11~50",
        "51~100",
        "101",
      ],
      email: "",
      password: "",
      password2: "",
      m_name: "",
      c_name: "",
      c_num: "",
      phone: "",
      industry: "",
      workers: "",
      username:""
    };
  },
  created() {
    this.read_data();//
  },
  methods: {
    back_to_login() {
      this.$router.push("/view/view_home"); ///view/view_login
    },
    read_data() {
      let password = localStorage.pa;
      let username = localStorage.view_username;
      this.axios
        .post("/rise_api/public/index.php/view/select", {
          username: username,
          password: password,
        })
        .then((response) => {

          console.log(response.data["0"]);
          this.username = response.data["0"]["username"]; //
          this.email = response.data["0"]["email"];
          this.password = response.data["0"]["password"]; //password
          this.password2 = response.data["0"]["password"];
          this.m_name = response.data["0"]["name"]; //
          this.c_name = response.data["0"]["cname"]; //
          this.c_num = response.data["0"]["cnum"]; //
          this.phone = response.data["0"]["phone"]; //
          this.workers = response.data["0"]["workers"]; //
          this.industry = response.data["0"]["industry"]; //
        });
    },
    update() {
      if(this.password==this.password2){
        this.axios
        .put(
          "/rise_api/public/index.php/view/update_data",
          {
            email: this.email,
            password: this.password,
            m_name: this.m_name,
            c_name: this.c_name,
            c_num: this.c_num,
            phone: this.phone,
            workers: this.workers,
            industry: this.industry,
            v_username: localStorage.view_username,
          }
        )
        .then((response) => {

          this.$swal({
            icon: "success",
            title: "!",
          });
        });
      }else{
        this.$swal({
            icon: "error",
            text: "!"
          });
      }

    },
  },
};
</script>

<style>
#all_2 {
  width: 90%;
  margin: auto;
}
#all_3 {
  width: 70%;
  margin: auto;
}
.form-signin input[type="email"] {
  /*  border-bottom-right-radius: 0;
  border-bottom-left-radius: 0;*/
  text-indent: 35px;
  background-size: 30px 35px;
  background-image: url("../assets/email.png");
  background-position: 7px 4px;
  background-repeat: no-repeat;
}
.form-signin input[type="password"] {
  /*  border-top-left-radius: 0;
  border-top-right-radius: 0;*/
  text-indent: 35px;
  background-size: 25px 25px;
  background-image: url("../assets/password.png");
  background-position: 9px 9px;
  background-repeat: no-repeat;
}
.bbbox {
  border: 1px solid #cccccc;
  width: auto;
  height: 200px;
  background-color: #ffffff;
  padding: 5px;
  overflow: auto;
}
.myMOUSE:hover {
  cursor: pointer;
  color: #0056c2;
  text-decoration: underline;
}
</style>



